*{ box-sizing: border-box;}
body{background-color: #000; color: #fff; font-family: Tahoma; font-size: 10px;}
#container{ background-color: #fff; border-radius: 45px; width:280px; height: 565px; margin: 0 auto; padding:60px 20px 70px 20px;
    &:before{ content: ' '; width: 5px; height: 5px; border-radius: 50%; background-color: #353535; margin: -40px auto 30px auto; display: block;}
    &:after{ content: ' '; width: 40px; height: 40px; border-radius: 50%; background-color: #353535; margin: 20px auto; display: block; align-items: center;}
    #tela{width:240px; height: 435px; background-color: #353535; display: flex; flex-direction: column;
        #page1, #page2{width: 100%; height: 100%;}
        header{position:relative; display: flex; padding: 5px; height: 15px;
            span{align-self: flex-start; height: 5px; width: 5px; background-color: #fff; display: inline-block; margin: 0 1px; border-radius: 50%;}
            .sinal{flex-grow: 1;}
            .hora{flex-grow: 1; text-align: center;}
            .bateria{flex-grow: 1; text-align: right;}
        }
        footer{align-self: flex-end; height: 30px; width: 100%; padding: 5px; position: relative;
            i{font-size: 20px; position: absolute; right: 5px;}
            &:after{content: ' '; display: block; width: 40px; height: 5px; margin:13px auto 0px auto; border-radius: 5px; background-color: #747474;}
        }
        
        .content{height: calc(100% - 45px); display: flex; justify-content: center;  flex-direction: column;
            .btn-desbloquear{color: #747474; cursor: pointer; background-color: #fff; width: 175px; height: 35px; margin: 10px auto; align-self: flex-end; border-radius: 15px; text-align: center; line-height: 35px; font-size: 14px; transition:all 0.3s ease;
                &:hover{  -webkit-transform: scale(0.9);  -ms-transform: scale(0.9);  transform: scale(0.9);}
            }
            .relogio{flex-grow: 1; padding-top:50px;}
            .relogioFull{font-size: 50px; text-align: center; display: block;}
            .data{ display: block; text-align: center; }
        }

        #page2 #usuario{text-align: center;
            figure{width: 160px; height: 160px; border-radius: 50%; background-color: #000; overflow: hidden; margin-top: 30px;}
            .imagem{width: 100%;}
            h2{font-size: 24px; font-weight: normal;}
            h3{font-size: 12px; font-weight: normal;}
        }
    }
}
.hide{display: none;}